import React, {useState, useEffect, useRef} from "react";
import API from "../../API";
import SanitizedHTML from "react-sanitized-html";
import AwesomeSlider from "react-awesome-slider";
import "react-awesome-slider/dist/styles.css";
import BlogBanner from "../../components/blog-banner";
import SexBlogBg from "../../images/sexBlogBg.png";

import {
    FacebookShareButton,
    TelegramShareButton,
    VKShareButton,
    WhatsappShareButton,
    FacebookIcon,
    VKIcon,
    TelegramIcon,
    WhatsappIcon,
} from "react-share";

import "./index.css";

export default function SexArticle(props) {
    const [articleInfo, setArticleInfo] = useState([]);
    const articleRef = useRef()

    useEffect(() => {
        window.scrollTo(0, 0);

        const fetchData = async () => {
            const result = await API.getArticleByKey(
                props.match.params.sexBlogArticleId
            );
            console.log(result.data.blog);
            setArticleInfo(result.data.blog);
        };
        fetchData();
        // eslint-disable-next-line
    }, []);


    return (
        <div ref={articleRef}>
            <BlogBanner
                blogBannerInfo={{
                    header: articleInfo.title,
                    backImg: SexBlogBg,
                    backColor: "#63308F",
                }}
            />
            <div className="base-container base-padding blog-description">
                <SanitizedHTML
                    allowedTags={false}
                    allowedAttributes={false}
                    html={articleInfo.content}
                />

                {articleInfo.images && (
                    <AwesomeSlider infinite={true} bullets={false}>
                        {articleInfo.images.images.map(
                            (item, id) => (
                                <div key={id}>
                                    <img src={item} alt={id}/>
                                </div>
                            )
                        )}
                    </AwesomeSlider>
                )}
                <div className="article-social flex-row w-100 justify-end align-end">
                    <FacebookShareButton children={articleRef}
                                         quote={articleInfo.content?.replace(/(<([^>]+)>)/gi, "").replace(/&nbsp;/g, " ").substring(0, 200) + "..."}
                                         url={`https://sexedu.rhak.kg/sexblog/${props.match.params.sexBlogArticleId}`}>
                        <FacebookIcon round={true}/>
                    </FacebookShareButton>
                    <VKShareButton children={articleRef}
                                   title={articleInfo?.content?.replace(/(<([^>]+)>)/gi, "").replace(/&nbsp;/g, " ").substring(0, 200) + "..."}
                                   url={`https://sexedu.rhak.kg/sexblog/${props.match.params.sexBlogArticleId}`}
                                   image={articleInfo?.photo || ""}>
                        <VKIcon round={true}/>
                    </VKShareButton>
                    <TelegramShareButton children={articleRef}
                                         title={articleInfo?.content?.replace(/(<([^>]+)>)/gi, "").replace(/&nbsp;/g, " ").substring(0, 200) + "..."}
                                         url={`https://sexedu.rhak.kg/sexblog/${props.match.params.sexBlogArticleId}`}>
                        <TelegramIcon round={true}/>
                    </TelegramShareButton>
                    <WhatsappShareButton children={articleRef}
                                         title={articleInfo?.content?.replace(/(<([^>]+)>)/gi, "").replace(/&nbsp;/g, " ").substring(0, 200) + "..."}
                                         url={`https://sexedu.rhak.kg/sexblog/${props.match.params.sexBlogArticleId}`}
                                         separator=" --- ">
                        <WhatsappIcon round={true}/>
                    </WhatsappShareButton>
                </div>
            </div>
        </div>
    );
}
